@import (less) "color.less";
:host {
    position: relative;
    display: block;
}

:host(.ng-invalid.nbp-show-validation) {
    .dropdown-input-box {
        border: 2px solid @red !important;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
        &:focus {
            border: 2px solid @red !important;
        }
    }
}

.highlight-default {
    border: 2px solid @yellow !important;
    outline: 0;
    box-shadow: none !important;
}

.dropdown-container {
    display: inline-block;
    position: relative;
    width: 100%; // margin-bottom: 1.563em;
    ul {
        opacity: 1;
        background: @white;
        border-top-width: 0px;
        border-right-color: @darkest_gray;
        border-right-style: solid;
        border-right-width: 1px;
        border-bottom-color: @darkest_gray;
        border-bottom-style: solid;
        border-bottom-width: 1px;
        border-left-color: @darkest_gray;
        border-left-style: solid;
        border-left-width: 1px;
        border-radius: 0 0 2px 2px;
        list-style: none;
        margin: 0 0 0.625em 0;
        padding: 0;
        position: absolute;
        width: 100%; // max-height: 11.938em;
        z-index: 1000;
        overflow-y: auto;
        >li {
            cursor: pointer;
            min-height: 3.125em;
            padding: 17px 12px;
            border-bottom: 1px solid @lightest_gray;
        }
        >li:hover {
            background: @lightest_green;
        }
        >li.active {
            background: @light_green;
        }
        >li.last {
            border-bottom: none;
        }
    }
    ul.ng-hide-add {
        transition: all ease-out 0.25s;
    }
    ul.ng-hide-remove {
        transition: all ease-out 0.25s;
    }
    ul.ng-hide {
        opacity: 0;
        max-height: 0;
    }
}

.dropdown-inner-container {
    position: relative;
    z-index: 999;
}

.dropdown-label {
    color: @gray;
    font-size: 1em;
    margin: 0 0 0 0.750em !important;
}

.dropdown-input-box {
    // margin: 0.625em 0 0 0;
    margin: 0;

    /*unselectable text*/
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    /*prima soluzione per evitare testo troppo lungo nelle combobox
    padding: 0.375em 3em 0.375em 1em;*/
    /*oppure con ellipse*/
    padding: 0.375em 2.3em 0.375em 1em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: @gray;
    border: 1px solid @darkest_gray;
    border-radius: 2px;
    font-size: 1em !important;
    height: 3.125em;
    &:focus {
        border: 2px solid @light_green;
        outline: 0;
        box-shadow: none !important;
    }
    &::-ms-clear {
        display: none;
    }
    &:disabled {
        background-color: @white !important;
        cursor: default;
        box-shadow: none !important;
    }
    &.input-box-disabled {
        background-color: @white !important;
        border: 1px solid @gray;
        opacity: 0.5 !important;
        cursor: not-allowed;
    }
}

.dropdown-input-box-green {
    border: 2px solid @light_green;
    outline: 0;
    box-shadow: none !important;
}

.dropdown-input-box-disabled {
    background-color: @white !important;
    opacity: 0.5 !important;
}

.dropdown-button {
    display: table;
    position: absolute;
    top: 0;
    right: 0;
    width: 2.5em;
    height: 3.125em;
    text-align: center;
    color: @light_gray;
    border-color: transparent;
    background-color: transparent !important;
    span {
        display: table-cell;
        vertical-align: middle;
    }
    &[role=""] {
        cursor: default;
        opacity: 0.5;
    }
    &.collapse-disabled {
        pointer-events: none;
    }
    &.collapse-disabled .dropdown-button-border {
        border-left: 1px solid @gray !important;
    }
    &.collapse-disabled .triangolo-bottom {
        border-style: solid;
        border-width: 8px 6.5px 0 6.5px;
        border-color: @gray transparent transparent transparent;
    }
    &.collapse-disabled .triangolo-top {
        border-style: solid;
        border-width: 0 6.5px 8px 6.5px;
        border-color: transparent transparent @gray transparent;
    }
}

.dropdown-button-border {
    position: absolute;
    display: inline-block;
    height: 60%;
    top: 20%;
    left: 0%;
    border-left: 1px solid @darkest_gray;
}

.has-error {
    .dropdown-input-box {
        border: 2px solid @red;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
        &:focus {
            border: 2px solid @red;
        }
    }
}

.dropdown-error-message {
    color: @red;
    font-size: 1em;
    margin-top: 0.625em;
    margin-bottom: 0.625em;
    opacity: 1;
}

.dropdown-error-message.ng-hide-add {
    transition: all ease-out 0.25s;
}

.dropdown-error-message.ng-hide-remove {
    transition: all ease-out 0.25s;
}

.dropdown-error-message.ng-hide {
    opacity: 0;
    margin-top: -0.625em;
}

.triangolo-top {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent @green transparent;
    display: inline-block !important; //margin-top: 30%;
}

.triangolo-bottom {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 10px 0 10px;
    border-color: @green transparent transparent transparent;
    display: inline-block !important; //margin-top: 30%;
}

.yellow-border {
    border: 2px solid @yellow;
}

